import React , { Component } from 'react';
import Grid from '@material-ui/core/Grid';


//Components
import Scream from '../components/Scream';
import Categories from '../components/Categories';
import Countries from '../components/Countries';
import NewsSkeleton from '../components/NewsSkeleton';
import Hidden from '@material-ui/core/Hidden';
import Typography from '@material-ui/core/Typography';

// mui stuff
const NewsAPI = require('newsapi');
const newsapi = new NewsAPI('9047eaa819904eb49205d2d53ab54356');
var networkDataReceived = false;
    
// To query /v2/top-headlines
// All options passed to topHeadlines are optional, but you need to include at least one of them


class home extends Component {
    constructor(props){
        super(props);
        this.state = {
            loading: true,
            error: false,
            theError: '',
            news: []
        }
    }
    
    fetchOnline = () =>{
        return newsapi.v2.topHeadlines({
        language: 'en',
        pageSize: 50
        }).then(response => {
            if(response.status === 'ok'){
                networkDataReceived = true;
                this.setState({
                    loading: false,
                    news: response.articles
                })
            } else{
                this.setState({
                    error: true,
                    theError: "Sorry! Problem loading new headlines!"
                })
            }
        })
        .catch((err)=>{
            console.log(err)
            this.setState({
                error: true,
                theError: "Offline: Turn on data for latest headlines."
            })
        });
    }
    UNSAFE_componentWillMount(){
        // fetch cached data
        const This = this;
        console.log("This was invoked, func!")
        caches.open('mysite-dynamic').then((cache)=>{
        console.log("This was invoked, cache!")
        console.log(cache.json())
            cache.match('https://newsapi.org/v2/top-headlines?language=en&pageSize=50',{ignoreMethod:true,ignoreVary:true})
            .then(function(res) {
                if (!res) throw Error("No data");
                console.log("we got the good stuff")
                return res.json();
            }).then(function(response) {
                console.log(response)
                // don't overwrite newer network data
                if (!networkDataReceived) {
                    console.log("the state was set")
                    This.setState({
                        loading: false,
                        news: response.articles
                    })
                    console.log(This.state.news)
                }
            }).catch(function() {
                // we didn't get cached data, the network is our last hope:
                return This.fetchOnline();
            })
        }).catch(err=>{
            console.log(err)
            console.log("we got nothing there")
            This.setState({
                error: true,
                theError: "Sorry! Problem loading new headlines!"
            })
        })
    }
    componentDidMount(){
        //fetch fresh data
        return this.fetchOnline();
    }
    render (){
        const { loading, news, error, theError } = this.state;
        let fetchError = error?(<Typography  variant="body2" color="error">{theError}</Typography>):null;
        let recentScreamMarkUp = !loading?news.map((scream,ind)=><Scream key={`${scream.title}${ind}`} scream={scream}/>):(<NewsSkeleton />)
        return (
            <Grid container spacing={1}>
                {fetchError}
                <Grid item md={10} sm={9} xs={12}>
                    {recentScreamMarkUp}
                </Grid>
                <Hidden xsDown>
                    <Grid  item md={2} sm={3} xs={12}>
                        <Categories category='All' country='All' urlTo='/worldnews-client/category' />
                        <Countries country='All' category='All'  urlTo='/worldnews-client/country' />
                    </Grid>
                </Hidden>
            </Grid>
        )
    }
}

export default home;